}

.app {
  background-image: url("/static/media/Keda Tech Assessment BG.43faa11c.svg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-top: 60px;
  padding-top: 1rem;
}

.page {
  min-height: 90vh;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (min-width: 768px) {
  .app {
    background-size: cover;
